:default:root {
    --img_Cont_height: 0vh;
    --img_height: 0vh;
}

/* #6079AB */

@keyframes swipe-in {
    0%   {left:160%;}
    100%  {left:0%;}
}

@keyframes swipe-out {
    0%   {left:0%;}
    100%  {left:160%;}
}

body {
    margin: 0;
    text-align: center;
    font-size: x-large;
    font-weight: bold;
}

.default, .Counter, .Totals, #Notification {
    background-color: #6079AB;
    color: white;
    border-radius: 10px;
    border-width: 0px;
    height: 40px;
    font-weight: bold;
    border: 2px solid black;
}

.default {
    width: 125px;
    font-size: medium;
}

.Counter {
    width: 50px;
    font-size: 2e1;
}

#Notification, .Totals {
    width: 200px;
    font-size: large;
    text-align: center;
    line-height: 40px;
}

.notif-in {
    animation-name: swipe-in;
    animation-duration: 2.5s;
    animation-timing-function: ease;
    position: relative;
}

.notif-out {
    animation-name: swipe-out;
    animation-duration: 2.5s;
    animation-timing-function: ease;
    position: relative;
}

#Prod_Info_Table {
    position: absolute;
    height: 60vh;
    width: 25vw;
    left: -25vw;
    border-collapse: collapse;
}

#Prod_Info_Table > tr {
    height: 10%;
}

#Prod_Info_Table > tr > td {
    border: 2px solid black;
}

#Basket_Table {
    position: absolute;
    width: 80vw;
    border-collapse: collapse;
}

#Headers {
    height: 10vh;
}

#Basket_Table, #Basket_Table > tr, #Headers > th {
    border: 2px solid #000000;
}

.Prod_IMG_Container, #Prod_IMG_Div, #Prod_IMG {
    height: 15vh;
}

#PopUp_Table {
    background-color: #ededed;
    width: 30vw;
    border-collapse: collapse;
}

#PopUp_Table > tr > td {
    border: 3px solid #000000;
}


#Total_Price, #Total_Quant, #Diff_Quant, #Checkout_Button, #BFB {
    position: absolute;
    left: calc(90vw - 100px);
}

#Checkout_Button, #BFB {
    line-height: 0px;
}

