:default:root {
  --img_Cont_height: 0vh;
  --img_height: 0vh;
}

@keyframes swipe-in {
  0% {
    left: 160%;
  }

  100% {
    left: 0%;
  }
}

@keyframes swipe-out {
  0% {
    left: 0%;
  }

  100% {
    left: 160%;
  }
}

body {
  text-align: center;
  margin: 0;
  font-size: x-large;
  font-weight: bold;
}

.default, .Counter, .Totals, #Notification {
  color: #fff;
  background-color: #6079ab;
  border: 2px solid #000;
  border-radius: 10px;
  height: 40px;
  font-weight: bold;
}

.default {
  width: 125px;
  font-size: medium;
}

.Counter {
  width: 50px;
  font-size: 20px;
}

#Notification, .Totals {
  text-align: center;
  width: 200px;
  font-size: large;
  line-height: 40px;
}

.notif-in {
  animation-name: swipe-in;
  animation-duration: 2.5s;
  animation-timing-function: ease;
  position: relative;
}

.notif-out {
  animation-name: swipe-out;
  animation-duration: 2.5s;
  animation-timing-function: ease;
  position: relative;
}

